<template>
  <div id="home">
  	<div id="home-inner" @mouseleave="slideIn()">
  		<div id="left-text" class="sliding-component">
        <div id="left-text-inner">{{ sectionTitle }}</div>
      </div>
  		<div id="center-content">
  			<div id="img" v-bind:style="getBackground()"  @mouseover="slideOut()"></div>
  			<div id="words">{{ word }}</div>
  		</div>
  		<div id="right-text" class="sliding-component">
  			<HomeNavigation v-show="!isMobile"></HomeNavigation>
        <div id="mobile-right-text"v-show="isMobile">Engineer.</br>Designer.</br>Artist.</div>
      </div>
  	</div>
  </div>
</template>

<script>
import { TweenMax } from 'gsap';
import HomeNavigation from '@/components/HomeNavigation'

export default {
  name: 'Home',
  components: { HomeNavigation },
  data() {
  	return {
      isMobile: false,
      sectionTitle: '',
  		keywords: [
      "Artistic.",
      "Sensitive.",
      "Detail Oriented.",
      "Latte.",
      "Penrose triangle.",
      "YC.",
      "Introvert.",
      ],
      index: 0,
      timer: null,
      hovered: false,
      slideOut() {
        if (window.innerWidth >= 600) {
          this.hovered = true;
          TweenMax.to("#left-text", 0.2, {x: '-500', opacity: '1'});
          TweenMax.to("#right-text", 0.2, {x: '500', opacity: '1'});
        }
        /* For larger screen, timer starts when user hover */
        const self = this;
        this.timer = setInterval(this.getKeywordIndex(self), 5000);
      },
      slideIn() {
        if (window.innerWidth >= 600) {
          this.hovered = false; 
          TweenMax.to("#left-text", 0.2, {x: '0', opacity: '0'});
          TweenMax.to("#right-text", 0.2, {x: '0', opacity: '0'});
        }
        clearInterval(this.timer);
        this.timer = null;
      },
      getBackground() {
        if(this.hovered || window.innerWidth < 600) {
          return { background: 'url(/static/me.jpeg) no-repeat center center', backgroundSize: 'cover' };
        } else {
          return { background: 'url(/static/me-bw.jpeg) no-repeat center center'};
        }
      },
      getKeywordIndex(self) {
        return () => {
          self.index = Math.floor((Math.random() * self.keywords.length));
        }
      }
  	}
  },
  computed: {
    word: function() {
      return (this.timer || this.isMobile) ? this.keywords[this.index] :  "Hover or scroll.";
    }
  },
  mounted() {
    if (window.innerWidth >= 600) {
      this.sectionTitle = "ABOUT YUJIA";
    } else  {
      this.sectionTitle = "YUJIA CAO";
      this.isMobile = true;

      /* For mobile, timer starts when page loaded */
      const self = this;
      this.timer = setInterval(this.getKeywordIndex(self), 5000);
    }
  },
  destroy() {
    /* destroy timer for mobile */
    if (window.innerWidth < 600) {
      clearInterval(this.timer);
      this.timer = null;
    }
  }
}
</script>

<style scoped>
@media screen and (max-width: 600px) {
  #home {
    width: 100%;
    height: 100vh;
  }

  #home-inner {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .sliding-component {
    margin: 0;
    position: static;
    opacity: 1;
  }

  #left-text {
    flex: 0 1 auto;
    width: 100%;
  }

  #left-text-inner {
    font-size: 3rem; 
    font-family: 'Cantata One', serif;
    text-align: center;
  }

  #center-content {
    position: relative;
    z-index: 998;
    height: 300px;
  }

  #words {
    position: absolute;
    width: 50%;
    height: 10%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    color: #fff;
    font-size: 1rem;
    text-align: center;
    font-family: 'Cantata One', serif;
  }

  #img {
    width: 100%;
    height: 100%;
    background-size: cover;
    -webkit-box-shadow: 14px 15px 36px -10px rgba(0, 0, 0, 0.55);
    -moz-box-shadow: 14px 15px 36px -10px rgba(0, 0, 0, 0.55);
    box-shadow: 14px 15px 36px -10px rgba(0, 0, 0, 0.55);
    z-index: 999;
  }

  #mobile-right-text {
    text-align: center;
    font-family: 'Cantata One', serif;
    font-size: 3rem;
  }  
}

@media screen and (min-width: 600px) {
  #home {
    width: 100%;
    height: 100vh;
    position: relative;

  }

  #home-inner {
    position: absolute;
    width: 100%;
    height: 80%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
  
  }

  .sliding-component {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    z-index: -1;
    opacity: 0;
  }

  #center-content { 
    width: 600px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    z-index: 998;
  }

  #left-text {
    width: 350px;
  }

  #right-text {
    width: 400px;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    z-index: 990;
  }

  #left-text-inner {
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0; 
    font-size: 12rem; 
    transform: rotate(-90deg) translateY(-100px);;
    transform-origin: 40% 40%;
    line-height: 90%;
    font-family: 'Cantata One', serif;
    text-align: center;
  }

  #img {
    width: 100%;
    height: 100%;
    background-size: cover;
    -webkit-box-shadow: 14px 15px 36px -10px rgba(0, 0, 0, 0.55);
    -moz-box-shadow: 14px 15px 36px -10px rgba(0, 0, 0, 0.55);
    box-shadow: 14px 15px 36px -10px rgba(0, 0, 0, 0.55);
    z-index: 999;
  }

  #words {
    position: absolute;
    width: 50%;
    height: 10%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    color: #fff;
    font-size: 2rem;
    text-align: center;
    font-family: 'Cantata One', serif;
  }
}
</style>



