<template>
  <div id="projects">
  	<div id="bg2"></div>
    <div id="mobile-title">
       <div id="mobile-title-bg"></div>
       <div id="mobile-title-text">PROJECTS</div>
    </div>
  	<div id="left-column">
  		<div id="project-container" v-for="(item, index) in leftProjects" :key="index">
            <div id="img-text-container">
              <img id="img" v-bind:src="'/static/' + item.image">
              <div id="text">
                <div id="title">{{item.name}}</div>
                <div id="description">{{item.description}}</div>
              </div>
            </div>
        </div>
        <div id="decoration-text" :key="4">See details on <a>Github</a>.</div>
  	</div>
  	<div id="right-column">
  		<div id="decoration-text">
  		    <div id="section-title" class="white">Projects.</div>
          <div id="bg"></div>
  	  </div>
  		<div id="project-container" v-for="(item, index) in rightProjects">
            <div id="img-text-container">
              <img id="img" v-bind:src="'/static/' + item.image">
              <div id="text">
                <div id="title">{{item.name}}</div>
                <div id="description">{{item.description}}</div>
              </div>
            </div>
        </div>
  	</div>
  </div>
</template>

<script>
import { Projects } from '../../static/projects.js'
import { TweenMax, TimelineMax } from 'gsap'
import Vue from 'vue'
import { Scene } from 'ScrollMagic'
import 'animation.gsap'

export default {
  name: 'Projects',
  components: {},
  data() {
  	return {
      show: false,
      leftProjects: Projects.slice(0, 3),
      rightProjects: Projects.slice(3)
  	}
  },
  mounted() {
  	var leftTween = TweenMax.staggerFrom("#left-column div", 0.05, { x: '-1000px', ease: Elastic.easeIn }, 0.02);
  	var rightTween = TweenMax.staggerFrom("#right-column div", 0.05, { x: '1000px', ease: Elastic.easeIn }, 0.02);
    var scene1 = new Scene({
         triggerElement: "#projects", 
         duration: 200
       })
      .setTween(leftTween)
      .addTo(this.$controller);

    var scene1 = new Scene({
         triggerElement: "#projects", 
         triggerHook: 0.6,
         duration: 200
       })
      .setTween(rightTween)
      .addTo(this.$controller);
  }
}
</script>

<style scoped>
#projects {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  transition: all 0.5s ease;
}

#mobile-title {
  display: none;
}

#projects #left-column, #projects #right-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
  padding: 0 3%;
}

#project-container {
  flex: 0;
  justify-content: center;
  padding: 4% 0;
  position: relative;
  width: 80%;
}

#img-text-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#img {
  flex: 1;
  height: 100%;
  max-width: 100px;
  width: 100%;
  -webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
  filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
} 

#img:hover {
  -ms-transform: scale(1.1); 
  -webkit-transform: scale(1.1); 
  transform: scale(1.1);
}

#text {
  vertical-align: middle;
  flex: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 3%;
}

@keyframes expand {
    from { width: 0; }
    to { width: 300%; }
}

@keyframes expand2 {
    from { width: 0; }
    to { width: 50%; }
}

#bg {
  position: absolute;
  background-color: #000;
  height: 60px;
  top: -30%;
  left: -20%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(20deg);
  -moz-transform: rotate(-10deg) skew(20deg);
  -o-transform: rotate(-10deg) skew(20deg);
   animation: expand 0.3s forwards;
}

#bg2 {
  position: absolute;
  background-color: #000;
  height: 15%;
  bottom: 10%;
  left: -8%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(50deg);
  -moz-transform: rotate(-10deg) skew(50deg);
  -o-transform: rotate(-10deg) skew(50deg);
   animation: expand2 0.3s forwards;
}

#decoration-text {
  font-family: 'Cantata One', serif;
  font-size: 3.2rem;
  height: 100px;
  position: relative;
  color: #fff;
}

#title {
	font-family: 'Cantata One', serif;
	font-size: 2rem;
}

@media screen and (max-width: 600px) {
  #projects {
    display: flex;
    flex-direction: column;
    height: auto;
  }

  #img {
    max-width: 50px;
  }

  #title {
    font-size: 1.5rem;
  }

  #description {
    font-size: 12px;
  }

  #mobile-title {
    display: block;
    position: relative;
    margin-top: 10%;
  }

  #mobile-title-bg {
    position: absolute;
    background-color: #000;
    height: 50px;
    width: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 5%;
    z-index: -1;
    -webkit-transform: rotate(-10deg) skew(20deg);
    -moz-transform: rotate(-10deg) skew(20deg);
    -o-transform: rotate(-10deg) skew(20deg);
  }

  #mobile-title-text {
    font-family: 'Cantata One', serif;
    font-size: 2rem;
    text-align: center;
    color: #fff;
  }

  #left-column, #right-column {
    flex: 1;
  }

  #bg, #bg2, #decoration-text {
    display: none;
  }
}
</style>



