<template>
  <div id="portfolio">
    <div id="section-title">
      <div id="bg"></div>
      <div id="text">Portfolio.</div>
    </div>
    <div id="section-title-mobile">
      <div id="bg"></div>
      <div id="section-text">PORTFOLIO</div>
    </div>
  	<div id="ftodo">
  		<div id="screenshot"></div>
  		<div id="icon-title">
  			<div id="icon">
  				<img id="img" src="/static/ftodo-green.png">
  			</div>
  			<div id="title">fTodo</div>
  		</div>
  		<div id="bg"></div>
  	</div>
  	<div id="supermath">
  		<div id="icon-title">
  			<div id='icon'>
  				<img id="img" src="/static/supermath.png">
  			</div>
  			<div id="title">SuperMath</div>
  		</div>
  		<div id="screenshot"></div>
  		<div id="bg"></div>
  	</div>
  	<div id="schedulehero">
  		<div id="screenshot"></div>
  		<div id="icon-title">
  			<div id='icon'>
  				<img id="img" src="/static/schedulehero.png">
  			</div>
  			<div id="title">ScheduleHero</div>
  		</div>
  		<div id="bg"></div>
  	</div>
  </div>
</template>

<script>
import { TweenMax, TimelineMax } from 'gsap'
import { Scene } from 'ScrollMagic'
import 'animation.gsap'

export default {
  name: 'Portfolio',
  components: {},
  mounted() {
  	var tween = new TimelineMax()
  	.from("#ftodo", 0.05, { x: '-1000px', ease: Elastic.easeIn })
    .from("#section-title", 0.03, { x: '1000px', ease: Elastic.easeIn })
  	.from("#supermath", 0.03, { x: '1000px', ease: Elastic.easeIn })
  	.from("#schedulehero", 0.03, { x: '-1000px', ease: Elastic.easeIn });

  	var scene = new Scene({
  		triggerElement: "#portfolio",
      triggerHook: 0.7,
  		duration: 200
  	})
	.setTween(tween)
	.addTo(this.$controller);
  }
}
</script>

<style scoped>
#portfolio {
	width: 100%;
	height: 100vh;
	display: flex;
	flex-direction: column;
  position: relative;
}

#section-title-mobile {
  display: none;
}

#section-title {
  position: absolute;
  top: 10%;
  right: -1%;
  width: 20%;
}

#section-title #text {
  font-family: 'Cantata One', serif;
  font-size: 3rem;
  color: #fff;
}

@keyframes expand {
    from { width: 0; }
    to { width: 120%; }
}

#section-title #bg {
  position: absolute;
  background-color: #000;
  height: 60px;
  top: 10%;
  right: -5%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(20deg);
  -moz-transform: rotate(-10deg) skew(20deg);
  -o-transform: rotate(-10deg) skew(20deg);
   animation: expand 0.3s forwards;
}

#ftodo, #supermath, #schedulehero {
	flex: 1;
	position: relative;
	display: flex;
}

#screenshot, #icon-title {
	flex: 1;
}

#title {
	font-family: 'Cantata One', serif;
  font-size: 3rem;
  color: #000;
  padding: 2%;
}

#screenshot:hover, #img:hover {
  padding-left: 1%;
}

/* ftodo */

#ftodo #screenshot {
	height: 120%;
}

#ftodo #screenshot::before {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  background: url('/static/ftodo-screenshot.png') no-repeat center center;
  background-size: contain;
  -webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
    filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
}

@keyframes expand-ftodo {
    from { width: 0; }
    to { width: 60%; }
}

#ftodo #bg {
  position: absolute;
  background-color: #4dc5ac;
  height: 60%;
  bottom: 10%;
  left: -10%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(50deg);
  -moz-transform: rotate(-10deg) skew(50deg);
  -o-transform: rotate(-10deg) skew(50deg);
  animation: expand-ftodo 0.3s forwards;
}

 #ftodo #icon-title {
 	display: flex;
 	justify-content: flex-start;
 	align-items: center;
 }

#ftodo #img {
	height: 50px;
    width: 50px;
	-webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
    filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
}

#ftodo #icon-title * {
	flex: 0 1 auto;
}

/*  supermath */

#supermath #screenshot {
	height: 120%;
	top: -30%;
}

#supermath #screenshot::before {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  background: url('/static/supermath-screenshot.png') no-repeat center center;
  background-size: contain;
  -webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
  filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
}

@keyframes expand-supermath {
  from { width: 0; }
  to { width: 60%; }
}

#supermath #bg {
  position: absolute;
  background-color: #4860e7;
  height: 70%;
  top: 0;
  right: -10%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(40deg);
  -moz-transform: rotate(-10deg) skew(40deg);
  -o-transform: rotate(-10deg) skew(40deg);
  animation: expand-supermath 0.3s forwards;
}

 #supermath #icon-title {
 	display: flex;
 	justify-content: flex-end;
 	align-items: center;
}

#supermath #img {
	height: 50px;
  width: 50px;
	-webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
  filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
}

#supermath #icon-title * {
	flex: 0 1 auto;
}

/* schedulehero */

#schedulehero #screenshot {
	flex: 1;
	height: 120%;
  transform: translateY(-80px);
}

#schedulehero #screenshot::before {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  background: url('/static/schedulehero-screenshot.png') no-repeat center center;
  background-size: contain;
  -webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
  filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
}

@keyframes expand-schedulehero {
  from { width: 0; }
  to { width: 50%; }
}

#schedulehero #bg {
  position: absolute;
  background-color: #ff5647;
  height: 80%;
  top: 5%;
  left: -10%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(50deg);
  -moz-transform: rotate(-10deg) skew(50deg);
  -o-transform: rotate(-10deg) skew(50deg);
  animation: expand-schedulehero 0.3s forwards;
}

 #schedulehero #icon-title {
 	flex: 2;
 	display: flex;
 	top: -15%;
 	justify-content: flex-start;
 	align-items: center;
 }

#schedulehero #img {
	height: 50px;
  width: 50px;
	-webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
  filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
}

#schedulehero #icon-title * {
	flex: 0 1 auto;
}

@media screen and (max-width: 600px) {
  #portfolio {
    height: 120vh;
  }

  #ftodo, #supermath, #schedulehero {
    flex-direction: column;
    margin: 20px 0;
  }

  #supermath {
    flex-direction: column-reverse;
  }

  #ftodo #icon-title, #supermath #icon-title, #schedulehero #icon-title {
    justify-content: center;
    flex: 1;
  }

  #ftodo #screenshot, #supermath #screenshot, #schedulehero #screenshot {
    flex: 3;
  }

  #ftodo #bg, #supermath #bg, #schedulehero #bg {
    width: 150%;
    flex: 3;
    animation: none;
  }

  #ftodo #img, #supermath #img, #schedulehero #img {
    width: 25px;
    height: 25px;
  }

  #section-title-mobile {
    display: inline;
    width: 100%;
    padding-top: 20px;
  }

  #section-title {
    display: none;
  }

  #section-text {
    font-family: 'Cantata One', serif;
    font-size: 2.5rem;
    color: #fff;
    text-align: center;
  }

  @keyframes expand {
    from { width: 0; }
    to { width: 80%; }
  }

  #section-title-mobile #bg {
    position: absolute;
    background-color: #000;
    height: 50px;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 5%;
    z-index: -1;
    -webkit-transform: rotate(-10deg) skew(20deg);
    -moz-transform: rotate(-10deg) skew(20deg);
    -o-transform: rotate(-10deg) skew(20deg);
    animation: expand 0.3s forwards;
  }

  #title {
    font-family: 'Cantata One', serif;
    font-size: 1.6rem;
    color: #000;
    padding: 2%;
  }
}

</style>



