// penrose triangle modified from code written by Louis Hoebregts
import { Detector } from './Detector';
import * as THREE from 'three';

var windowWidth = window.innerWidth;
var windowHeight = window.innerHeight;
var windowHalfX = window.innerWidth / 2;
var windowHalfY = window.innerHeight / 2; 

var mouseX = 0
var mouseY = 0;

var container;
var renderer;
var triangle;
var camera;
var scene;
var control;

export function init() {
  if ( ! Detector.webgl ) Detector.addGetWebGLMessage();

  container = document.getElementById('canvas');

  camera = new THREE.PerspectiveCamera( 45, windowWidth / windowWidth, 1, 3000 );
  camera.position.set(1100, 1100, 1050);
  camera.lookAt(new THREE.Vector3(0,0,0));
  camera.aspect = windowWidth / windowHeight;
  camera.updateProjectionMatrix();

  renderer = new THREE.WebGLRenderer({
     canvas: container,
     antialias: true,
     alpha: true
   });
  renderer.setPixelRatio(window.devicePixelRatio > 1 ? 2 : 1);
  renderer.setSize(container.offsetWidth, container.offsetHeight);
/*
  controls = new THREE.OrbitControls( camera, renderer.domElement );
  controls.addEventListener( 'change', render );
  controls.enableZoom = false;
*/
  scene = new THREE.Scene();
  triangle = new THREE.Object3D();
  scene.add(triangle);

  var cube = 80;
 
  var mat = new THREE.MeshLambertMaterial({
    color:0xffffff
  });
 
 //top side
  var geom = new THREE.BoxGeometry(cube * 5, cube * 1.4, cube * 1.2);
  var mesh = new THREE.Mesh(geom, mat);
  mesh.position.z = cube / 10;
  triangle.add(mesh);
 
 //bottom side
  var geom = new THREE.BoxGeometry(cube, cube * 1.2, cube * 5);
  var mesh = new THREE.Mesh(geom, mat);
  mesh.position.x = cube * 2.63;
  mesh.position.y = cube / 10;
  mesh.position.z = cube * 2;
  triangle.add(mesh);
 
 //left side
  var geom = new THREE.BoxGeometry(cube, cube * 5.5, cube / 1.25);
  var mesh = new THREE.Mesh(geom, mat);
  mesh.position.x = cube * 3;
  mesh.position.y = cube * 2.75;
  mesh.position.z = cube * 4.9;
  triangle.add(mesh);
 
 //two flat square
  var geometry = new THREE.PlaneGeometry( cube * 1.6, cube / 1.3, cube );
  var plane = new THREE.Mesh( geometry, mat );
  plane.position.set(cube * 4.8, cube * 6.08, cube * 6.2)
  triangle.add(plane);
 
  var geometry = new THREE.PlaneGeometry( cube / 1.2, cube / 1.7, cube );
  var mat = new THREE.MeshBasicMaterial({ color: 0xf7f7f7 })
  var plane = new THREE.Mesh( geometry, mat );
  plane.position.set(cube * 5.2, cube * 6.83, cube * 6.25)
  triangle.add(plane);

//light
  var directionalLight = new THREE.DirectionalLight( 0xc6c6c6, 1 );
  directionalLight.position.x = 10;
  scene.add(directionalLight);
  var directionalLight = new THREE.DirectionalLight( 0xd6d6d6, 1 );
  directionalLight.position.y = 10;
  scene.add(directionalLight);
  var directionalLight = new THREE.DirectionalLight( 0x939393, 1 );
  directionalLight.position.z = 10;
  scene.add(directionalLight);

  //container.appendChild( renderer.domElement );

//YC
  var yRight = new THREE.PlaneGeometry( cube * 2.3, cube / 3, cube );
  var mat2 = new THREE.MeshBasicMaterial({ color: 0x000 })
  var plane2 = new THREE.Mesh( yRight, mat2 );
  plane2.position.set(cube * 4.88, cube * 5.5, cube * 6.25);
  plane2.rotation.z = 2.5;
  triangle.add(plane2);

  var yLeft = new THREE.PlaneGeometry( cube, cube / 5, cube );
  var mat3 = new THREE.MeshBasicMaterial({ color: 0x000 })
  var plane3 = new THREE.Mesh( yLeft, mat3 );
  plane3.position.set(cube * 4.4, cube * 5.3, cube * 6.25);
  plane3.rotation.z = 3.2;
  triangle.add(plane3);

  var cLeft = new THREE.PlaneGeometry( cube * 2.3, cube / 3, cube );
  var mat4 = new THREE.MeshBasicMaterial({ color: 0x000 })
  var plane4 = new THREE.Mesh( cLeft, mat4 );
  plane4.position.set(cube * 5.69, cube * 5.65, cube * 6.25);
  plane4.rotation.z = 2.5;
  triangle.add(plane4);

  var cTop = new THREE.PlaneGeometry( cube * 1.8, cube / 5, cube );
  var mat5 = new THREE.MeshBasicMaterial({ color: 0x000 })
  var plane5 = new THREE.Mesh( cTop, mat5 );
  plane5.position.set(cube * 5.58, cube * 6.36, cube * 6.25);
  plane5.rotation.z = 3.15;
  triangle.add(plane5);

  var cBottom = new THREE.PlaneGeometry( cube * 2.6, cube / 7, cube );
  var mat6 = new THREE.MeshBasicMaterial({ color: 0x000 })
  var plane6 = new THREE.Mesh( cBottom, mat6 );
  plane6.position.set(cube * 7.37, cube * 6, cube * 6.25);
  plane6.rotation.z = 4.03;
  triangle.add(plane6);

  if (windowWidth >= 600) {
    document.addEventListener( 'mousemove', onDocumentMouseMove, false );
    window.addEventListener( 'resize', onWindowResize, false );
  }
}

function onDocumentMouseMove( event ) {
  event.preventDefault();
  var deltaX = event.clientX - mouseX;
  var deltaY = event.clientY - mouseY;
  mouseX = event.clientX;
  mouseY = event.clientY;
  rotateScene(deltaX, deltaY);
}


function rotateScene(deltaX, deltaY) {
    triangle.rotation.y = (triangle.rotation.y + deltaX / 100) / 1.2;
    //triangle.rotation.x += deltaY / 500;
}

export function animate() {
  requestAnimationFrame( animate );
  render();
}

function render(){
    renderer.render(scene, camera);
    if (windowWidth < 600) {
     triangle.rotation.y += 0.003;
    }
  }
 
function onWindowResize() {
  windowWidth = window.innerWidth;
  windowHeight = window.innerHeight;
  camera.aspect = windowWidth / windowHeight;
  camera.updateProjectionMatrix();

  renderer.setSize(windowWidth, windowHeight);
}


