<template>
  <div id="contact">
  	<div id="info">
  		<div id="info-inner">
  			<div id="text">
  			   <div id="hello-there">GET IN</br>TOUCH!</div>
  			   <div id="text-description">
  			  	 Hey! Thank you for stopping by. Let me know how do you think about this website here -></br> Or feel free to check out links below <3
  			   </div>
  		   </div>

  		    <div id="icons">
  		    	<div id="icons-list">
  		    		<div id="icon-element" v-for="item in icons" v-bind:style="{ background: 'url(/static/' + item.name + ') no-repeat center center', backgroundSize: 'cover' }">
                <a :href="item.url">
                </a>
              </div>
  		    	</div>
  		    </div>
  		</div>
  	</div>
  	<div id="form">
  		<div id="form-inner">
  			<div id="say-something">Say something?</div>
  			<form class="contact-form" action="https://formspree.io/yujiac8000@gmail.com" method="POST">
                 <input class="form-control name" type="text" name="name" 
                             required placeholder="Name">
         
                 <input class="form-control email" type="email" name="_replyto"
                             required placeholder="Email">
         
                 <textarea class="form-control message" name="message" placeholder="Message"
                                required rows="4">Hey! So I saw your personal site and...</textarea>
                 <input type="submit" value="Send" id="send">

                 <input type="hidden" name="_next" value="./" />
                 <input type="hidden" name="_subject" value="Contact from personal site" />
                 <input type="hidden" name="_format" value="plain" />
            </form>

            <div id="result">
             	<div id="fail">Oops...something happened. Message sending failed.</div>
              <div id="success">Got it. Thank you!</div>
            </div>
            <div id="bg"></div>
  		</div>
  	</div>
  </div>
</template>

<script>
import { TweenMax, TimelineMax } from 'gsap'
import { Scene } from 'ScrollMagic'
import 'animation.gsap'

export default {
  name: 'Contact',
  components: {},
  data() {
  	return {
  		icons: [
  		 { 
        name: "linkedin.png",
         url: "https://www.linkedin.com/in/yujia-cao-586363113/"
      },
      { 
        name: "github.png",
         url: "https://github.com/y77cao"
      },
      { 
        name: "pinterest.png",
         url: "https://www.pinterest.ca/y77cao/"
      },
      { 
        name: "instagram.png",
         url: "https://www.instagram.com/501notimplemented/"
      },
      { 
        name: "email.png",
         url: "mailto:yujiac8000@gmail.com"
      },
  		],
  	}
  },
  mounted() {
  	var infoOpacity = TweenMax.from("#info", 0.05, { opacity: '0'});
  	var formTween = TweenMax.staggerFrom("#form-inner", 0.05, { left: '1000px', ease: Elastic.easeIn }, 0.02);
    var scene1 = new Scene({
         triggerElement: "#contact", 
         duration: 300
       })
      .setTween(infoOpacity)
      .addTo(this.$controller);
    var scene2 = new Scene({
         triggerElement: "#contact",
         triggerHook: 1,
         duration: 300
       })
      .setTween(formTween)
      .addTo(this.$controller);
  }
}
</script>

<style scoped>
#contact {
	display: flex;
	width: 100%;
	height: 100vh;
}

#contact #info, #contact #form {
	flex: 1;
}

#form-inner, #info-inner {
	width: 80%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
  justify-content: center;
}

#form-inner * {
	margin: 2%;
	width: 90%;

}

#form-inner {
  margin: 0 auto;
  position: relative;
}

#say-something {
	padding-left: 2%;
	font-family: 'Cantata One', serif;
	font-size: 2.5rem;
	flex-grow: 0;
	color: #fff;
}

form input, form textarea {
	text-align: left;
  border: none;
  background-color: #ededed;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 1rem;
  padding: 2%;
}

form input:focus {
	outline: none;
	border: 1px solid black;
}

#send {
  background-color: #fff;
  color: #000;
  text-align: center;
  width: 20%;
  padding: 1%;
  align-self: center;
}

#result {
	margin-top: 0;
	padding: 0;
	position: relative;
	height: 50px;
}

#fail, #success {
	display: none;
	position: absolute;
	top: 0;
}


#info {
	display: flex;
    justify-content: center;
}

#hello-there {
	font-family: 'Cantata One', serif;
	font-size: 6rem;
}

#icons {
	flex: 0;
	width: 100%;
}

#icons-list {
	padding: 5% 0;
	width: 80%;
	height: 100%;
	display: flex;
	flex-direction: row;
  justify-content: space-between;
}

#icon-element {
	width: 25px;
	height: 25px;
}

#icon-element a {
  display: block;
  width: 25px;
  height: 25px;
}

#icon-element:hover {
	-ms-transform: scale(1.2); /* IE 9 */
  -webkit-transform: scale(1.2); /* Safari */
  transform: scale(1.2);
  cursor: pointer;
}

@keyframes expand {
    from { width: 0; }
    to { width: 100%; }
}

#bg {
    position: absolute;
    background-color: #000;
    height: 110%;
    top: 5%;
    right: -5%;
    z-index: -1;
    -webkit-transform: rotate(-20deg) skew(20deg);
    -moz-transform: rotate(-20deg) skew(20deg);
    -o-transform: rotate(-20deg) skew(20deg);
    animation: expand 0.3s forwards;
}


@media screen and (max-width: 600px) {
  #contact {
    flex-direction: column;
    height: auto;
  }

  #text div {
    text-align: center;
  }

  #text-description {
    font-size: 12px;
  }

  #icons-list {
    justify-content: space-around;
    padding: 5% 0;
    width: 100%;
  }

  #say-something {
    margin-top: 10%;
    text-align: center;
  }

  #form-inner {
    margin: 0 auto;
  }

  form * {
    margin-left: 0;
    padding-right: 0;
  }

  #form-inner * {
    align-items: center;
    font-size: 12px;
  }

  #hello-there {
    font-size: 3rem;
  }

  #say-something {
    font-size: 1.6rem;
    padding-top: 10%;
  }

  #bg {
    width: 120%;
    left: 10%;
    right: 0;
    animation: none;
  }

}
</style>



