<template>
  <div id="content" @click="showModal=false">
    <Modal v-show="showModal"></Modal>
    <Home></Home>
    <Projects></Projects>
    <Portfolio></Portfolio>
    <About></About>
    <Contact></Contact>
  </div>
</template>

<script>
import Modal from '@/components/Modal'
import Home from '@/components/Home'
import Projects from '@/components/Projects'
import Portfolio from '@/components/Portfolio'
import About from '@/components/About'
import Contact from '@/components/Contact'
import { EventBus } from '@/components/EventBus'

export default {
  name: 'Content',
  components: { Modal, Home, Projects, Portfolio, About, Contact },
  data() {
    return {
      showModal: true,
    }
  },
  mounted() {
  }
}
</script>

<style scoped>
#content {
  width: 100%;
  height: auto;
}

#content div {
  overflow: hidden;
}
</style>



