<template>
  <div id="about">
  	<canvas id="canvas"></canvas>
    <div id="about-section-title">
      <div id="bg"></div>
      <div id="text">About.</div>
    </div>
    <div id="section-title-mobile">
      <div id="bg"></div>
      <div id="section-text">ABOUT</div>
    </div> 
    <div id="text-container">
      <div id="left">
        <div>
          Hi, I am Yujia Cao.
        </div>
        <div>
          I am in my third year studying Computer Science at University of Waterloo. 
        </div>
        <div>
          I am a software engineer focusing on user experience.
        </div> 
        <div>
          I do product/UI/UX design on the side for interest. 
        </div>
      </div>
      <div id="right">
        <div>
          In my spare time, I also create art works, look for inspirations, look for good food, drink a lot of latte, learn new things, travel random places, take photos, edit photos and drink a lot of latte...(did I just say latte twice?)
        </div>
      </div>
    </div>  
  </div>
</template>

<script>
import { init, animate } from '../webgl/index'
import { TweenMax, TimelineMax } from 'gsap'
import { Scene } from 'ScrollMagic'
import 'animation.gsap'

export default {
  name: 'About',
  components: {},
  mounted() {
  	init();
    animate();
    var textTween = new TimelineMax()
    .from("#about-section-title", 0.05, { right: '-500px', ease: Elastic.easeIn }, 0.05)
    .from("#left", 0.05, { opacity: '0', ease: Elastic.easeIn }, 0.02)
    .from("#right", 0.05, { opacity: '0', ease: Elastic.easeIn }, 0.02)

    var scene = new Scene({
      triggerElement: "#about",
      triggerHook: 0.5,
      duration: 300
    })
    .setTween(textTween)
    .addTo(this.$controller);
  }
}
</script>

<style scoped>
#about {
  width: 100%;
  height: 100vh;
  z-index: -99;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

canvas {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -999;
}

#text-container {
  z-index: 0;
  color: #000;
  display: flex;
  flex-direction: row;
}

#text-container div {
  flex: 1;
}

#left {
  text-align: right;
  padding: 0 10%;
}

#right {
  text-align: left;
  padding: 0 10%;
}

#left div, #right div {
  width: 100%;
}

#about-section-title {
  position: absolute;
  top: 10%;
  right: -1%;
  width: 20%;
}

#section-title-mobile {
    display: none;
  }

#about-section-title #text {
  font-family: 'Cantata One', serif;
  font-size: 3rem;
  color: #fff;
}

@keyframes expand {
    from { width: 0; }
    to { width: 120%; }
}

#about-section-title #bg {
  position: absolute;
  background-color: #000;
  height: 60px;
  top: 10%;
  right: -5%;
  z-index: -1;
  -webkit-transform: rotate(-10deg) skew(20deg);
  -moz-transform: rotate(-10deg) skew(20deg);
  -o-transform: rotate(-10deg) skew(20deg);
   animation: expand 0.3s forwards;
}

@media screen and (max-width: 600px) {
  #text-container {
    flex-direction: column;
  }

  #left, #right {
    text-align: center;
    font-size: 12px;
  }

  #left {
    padding-bottom: 50px;
  }

  #right {
    padding-top: 50px;
  }


  #about-section-title {
    display: none;
  }

  #section-title-mobile {
    position: relative;
    display: inline;
    width: 100%;
    margin-bottom: 20px;
  }

  #section-text {
    font-family: 'Cantata One', serif;
    font-size: 2.5rem;
    color: #fff;
    text-align: center;
  }

  #section-title-mobile #bg {
    position: absolute;
    background-color: #000;
    height: 50px;
    width: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 5%;
    z-index: -1;
    -webkit-transform: rotate(-10deg) skew(20deg);
    -moz-transform: rotate(-10deg) skew(20deg);
    -o-transform: rotate(-10deg) skew(20deg);
  }
}
</style>



