<template>
  <nav id="home-navigation">
       <ul id="home-navigation-list">
         <li v-for="(item, index) in navigationOptions"
         >
          <a
             v-scroll-to="item.href"
             v-on:click="navigationTapped(item.name)"
             v-on:mouseover="onHover(index)"
             v-on:mouseleave="onHover(index)">{{item.name}}</a>
          <div id="hover" v-show="item.hover"></div>
        </li>
        <li>
          <a href="/static/resume.pdf"
             v-on:mouseover="resumeHover=!resumeHover"
             v-on:mouseleave="resumeHover=!resumeHover">RESUME</a>
          <div id="hover" v-show="resumeHover"></div>
        </li>
       </ul>
    </nav>
</template>

<script>
import { EventBus } from '@/components/EventBus'
export default {
  name: 'HomeNavigation',
   data() {
    return {
      navigationOptions: [
      { 
        name: 'PROJECTS',
        hover: false,
        href: '#projects'
      },
      { 
        name: 'PORTFOLIO',
        hover: false,
        href: '#portfolio'
      },
      { 
        name: 'ABOUT',
        hover: false,
        href: '#about'
      },
      {
        name: 'CONTACT',
        hover: false,
        href: '#contact'
      },
      ],
      resumeHover: false,
      onHover(index) {
        this.navigationOptions[index].hover = !this.navigationOptions[index].hover;
      },
      navigationTapped(name) {
        EventBus.$emit('navigation-tapped', name);
      }
    }
  },
  mounted() {
  }
}
</script>

<style scoped>
#home-navigation {
  font-family: 'Cantata One', serif;
  font-size: 2rem;
  width: 100%;
  height: 100%;
  z-index: 998;
}

#home-navigation-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style-type: none;
  width: 100%;
  height: 100%;
  z-index: 998;
}

#home-navigation-list li {
  padding: 0 20px;
  transition: 0.4s;
  z-index: 990;
  position: relative;
}

#home-navigation-list a {
  text-decoration: none;
  display: block;
  color: #000;
  z-index: -3;
}

@keyframes expand {
    from { width: 0; }
    to { width: 100%; }
}

#home-navigation-list #hover {
  height: 110%;
  background-color: #000;
  position: absolute;
  top: -10%;
  left: -5%;
  -webkit-transform: skew(5deg);
  -moz-transform: skew(5deg);
  -o-transform: skew(5deg);
  z-index: -2;
  animation: expand 0.3s forwards;
}

#home-navigation-list li:hover {
  z-index: 998;
}

#home-navigation-list li a:hover {
  color: #fff;
  font-size: 3.5rem;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  #home-navigation {
     padding-top: 50%;
  }

  #home-navigation-list li a:hover {
     color: #fff;
     -ms-transform: scale(1.2); 
     -webkit-transform: scale(1.2); 
     transform: scale(1.2);
  }
}
</style>



