<template>
  <div id="modal" >
    <div id="center-content">
       <div id="center-content-inner">
           <div id="construction"></div>
           <div id="construction-text">
               Hi! This site is under construction currently so 
               some of the features might not be available. Please wait for a while and 
               come back to see the full site. Not gonna be long<3. Thanks!
           </div>
           <div id="ok-btn">
             <div id="ok">OK</div>
             <div id="bg"></div>
           </div>
       </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Modal',
  components: {},
}
</script>

<style scoped>
#modal {
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	position: fixed;
	z-index: 999;
}

#center-content {
	position: absolute;
	height: 50%;
	width: 40%;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	background: #fff;
	border-radius: 10px;
	-webkit-filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
    filter: drop-shadow(8px 8px 8px rgba(0,0,0,0.2));
    transition: all 1s ease-in-out;
}

#center-content-inner {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#construction {
	width: 100px;
	height: 100px;
	background: url('/static/hammer.png') no-repeat center center;
	background-size: cover;
	margin: 5%;
}

#construction-text {
	color: #000;
	text-align: center;
	margin: 0 5%;
}

#ok-btn {
	position: relative;
	margin: 5%;
	cursor: pointer;
}

#ok {
	text-align: center;
	color: #fff;
}

@keyframes expand {
    from { width: 0; }
    to { width: 110%; }
}

#bg {
  height: 100%;
  background-color: #000;
  position: absolute;
  top: -10%;
  left: -5%;
  -webkit-transform: skew(5deg);
  -moz-transform: skew(5deg);
  -o-transform: skew(5deg);
  z-index: -2;
  animation: expand 0.3s forwards;
}

@media screen and (max-width: 600px) {
	#center-content {
	  height: 50%;
	  width: 90%;
	}
}
</style>



